___CSS_LOADER_EXPORT___.push([module.id, `/* Common Estimation Styles */
li button {
    font-size: 18px;
    padding: 15px;
    background-color: rgba(211, 211, 211, 0.211);
    margin: 10px;
    width: 70px;
    height: 80px;
    padding: 10px;
    border-radius: 10px;
    text-align: center;
    box-shadow: rgba(0, 0, 128, 0.25) 2px 2px 5px;
}


/* Body Styles */
#root {
    display: grid;
    grid: auto auto auto / auto auto auto;
}

/* Header Styles */
header {
    grid-row: 1;
    grid-column: 1 / span 3;
    height: 15vh;
    display: grid;
    grid: auto / 25vw;
    box-shadow: inset rgba(0, 0, 128, 0.25) 0px -7px 25px;
}

header>h1 {
    grid-row: 1 / span 2;
    grid-column: 2 / span 2;
    text-align: center;
    line-height: 15vh;
    font-size: 4rem;
}

header>h5 {
    padding: 25px;
    grid-row: 1 / span 2;
    grid-column: 1;
    text-align: left;
    font-size: 1rem;
}

header>a {
    grid-row: 1 / span 2;
    grid-column: 4;
    font-size: 1.5rem;
    width: 5rem;
    height: 2.5rem;
    background-color: red;
    text-align: center;
    line-height: 2.25rem;
    text-decoration: none;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 128, 0.25) 2px 2px 10px;
    margin: auto;
    color: black;
    margin-right: 40px;
    font-style: oblique;
}

/* Styles For the Current Queue */
#root>#currentQueue {
    grid-row: 2;
    grid-column: 2;
    width: 66vw;
    display: grid;
    grid: auto / auto;
    box-shadow: inset rgba(0, 0, 128, 0.25) 0px 0px 10px;
    height: 85vh;
}

section>div {
    display: grid;
    grid: auto / auto;
    width: 100%;
    height: 60px;
    padding-bottom: 10px;
    border-bottom: 1px navy solid;
}

#currentQueue>div>h3 {
    grid-column: 1;
    font-weight: 300;
    line-height: 40px;
    padding: 0px 20px;
    margin-left: 10vw;
    margin-top: 10px;
    text-align: right;
}

#currentQueue>div>h4 {
    grid-column: 2;
    font-size: 1.2rem;
    width: 11rem;
    padding: 10px;
    background-color: rgba(211, 211, 211, 0.211);
    border-radius: 10px;
    margin-top: 10px;
    height: 1.5rem;
    margin-right: 20vw;
    box-shadow: rgba(0, 0, 128, 0.25) 2px 2px 5px;

}

#currentQueue>ul {
    list-style-type: none;
    padding-left: 0px;
    grid-row: 3;
    grid-column: 1;
    display: flexbox;
    align-items: center;
    display: flex;
    justify-content: center;
    margin-top: 250px; /*Remove to add table*/
    gap: 30px;
}

#currentQueue>ul>li {
    width: 25px;
    display: inline-block;
    padding: 15px 7px;
    margin: 5px;
}

#currentQueue>h4 {
    grid-row: 2;
    width: 40px;
    padding: 40px 20px;
    background-color: rgba(211, 211, 211, 0.211);
    text-align: center;
    margin: auto;
    border-radius: 10px;
}

/* Story Queue Styles*/
#root #storyQueue {
    grid-row: 2 / span 2;
    grid-column: 1;
    height: 85vh;
    box-shadow: inset rgba(0, 0, 128, 0.25) 5px 0px 10px;
    width: 17vw;
}

h2 {
    text-align: center;
    padding: 10px 0px;
    font-size: 1.75rem;
    border-bottom: 1px solid navy;
    color: navy;
}

#storyQueue>h6 {
    font-size: 1rem;
    padding-left: 40px;
    margin: 10px 0px;
}

#storyQueue>ol {
    width: calc(75%);
    list-style-type: none;
    display: grid;
    grid: auto / auto;
}

#storyQueue>ol>li {
    font-size: 1.2rem;
    width: 8rem;
    margin: 0px;
    margin-bottom: 20px; 

}

#storyQueue li:nth-of-type(2) {
    padding: 0px 7px;
    margin: 0px;
    margin-bottom: 20px;
    margin-left: 10px;
    border-radius: 0px;
    text-align: center;
    height: 0px !important;
    border-bottom: 2px navy dashed;
    box-shadow: none;
}

/* Estimation Styles */
#root #estimations {
    grid-row: 2 / span 2;
    grid-column: 3;
    height: 85vh;
    box-shadow: inset rgba(0, 0, 128, 0.25) -5px 0px 10px;
    width: 17vw;
}

#estimations>ul {
    list-style-type: none;
    padding-left: 20px;
}

#estimations > ul > li {
    display: inline-block;
    width: 3rem;
}


/* Action Styles */
#storyQueue>ol>li>button:hover {
    letter-spacing: 1px;
}

header>a:hover,
#currentQueue>ul>li:hover {
    box-shadow: none;
}`, "",{"version":3,"sources":["webpack://./src/css/estimation.css"],"names":[],"mappings":"AAAA,6BAA6B;AAC7B;IACI,eAAe;IACf,aAAa;IACb,4CAA4C;IAC5C,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,6CAA6C;AACjD;;;AAGA,gBAAgB;AAChB;IACI,aAAa;IACb,qCAAqC;AACzC;;AAEA,kBAAkB;AAClB;IACI,WAAW;IACX,uBAAuB;IACvB,YAAY;IACZ,aAAa;IACb,iBAAiB;IACjB,qDAAqD;AACzD;;AAEA;IACI,oBAAoB;IACpB,uBAAuB;IACvB,kBAAkB;IAClB,iBAAiB;IACjB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,oBAAoB;IACpB,cAAc;IACd,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,oBAAoB;IACpB,cAAc;IACd,iBAAiB;IACjB,WAAW;IACX,cAAc;IACd,qBAAqB;IACrB,kBAAkB;IAClB,oBAAoB;IACpB,qBAAqB;IACrB,kBAAkB;IAClB,8CAA8C;IAC9C,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA,iCAAiC;AACjC;IACI,WAAW;IACX,cAAc;IACd,WAAW;IACX,aAAa;IACb,iBAAiB;IACjB,oDAAoD;IACpD,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,WAAW;IACX,YAAY;IACZ,oBAAoB;IACpB,6BAA6B;AACjC;;AAEA;IACI,cAAc;IACd,gBAAgB;IAChB,iBAAiB;IACjB,iBAAiB;IACjB,iBAAiB;IACjB,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,cAAc;IACd,iBAAiB;IACjB,YAAY;IACZ,aAAa;IACb,4CAA4C;IAC5C,mBAAmB;IACnB,gBAAgB;IAChB,cAAc;IACd,kBAAkB;IAClB,6CAA6C;;AAEjD;;AAEA;IACI,qBAAqB;IACrB,iBAAiB;IACjB,WAAW;IACX,cAAc;IACd,gBAAgB;IAChB,mBAAmB;IACnB,aAAa;IACb,uBAAuB;IACvB,iBAAiB,EAAE,sBAAsB;IACzC,SAAS;AACb;;AAEA;IACI,WAAW;IACX,qBAAqB;IACrB,iBAAiB;IACjB,WAAW;AACf;;AAEA;IACI,WAAW;IACX,WAAW;IACX,kBAAkB;IAClB,4CAA4C;IAC5C,kBAAkB;IAClB,YAAY;IACZ,mBAAmB;AACvB;;AAEA,sBAAsB;AACtB;IACI,oBAAoB;IACpB,cAAc;IACd,YAAY;IACZ,oDAAoD;IACpD,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,kBAAkB;IAClB,6BAA6B;IAC7B,WAAW;AACf;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,gBAAgB;IAChB,qBAAqB;IACrB,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;IACjB,WAAW;IACX,WAAW;IACX,mBAAmB;;AAEvB;;AAEA;IACI,gBAAgB;IAChB,WAAW;IACX,mBAAmB;IACnB,iBAAiB;IACjB,kBAAkB;IAClB,kBAAkB;IAClB,sBAAsB;IACtB,8BAA8B;IAC9B,gBAAgB;AACpB;;AAEA,sBAAsB;AACtB;IACI,oBAAoB;IACpB,cAAc;IACd,YAAY;IACZ,qDAAqD;IACrD,WAAW;AACf;;AAEA;IACI,qBAAqB;IACrB,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,WAAW;AACf;;;AA